@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600&family=Oswald:wght@300;400;500;600&display=swap);
* {
    margin: 0;
    padding: 0;
}
body {
    background-color: #fff;
    font-family: 'Open Sans', sans-serif;
}
.container {
    width: 1190px;
    margin: auto;
}
.text-accent {
    color: #00a8cc;
}
.text-white {
    color: #fff;
}


.Exchange {
    margin: 24px auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 360px;
}

.ExchangeButton {
    width: 120px;
    height: 60px;
    background-color: darkslateblue;
    box-shadow: 0px 4px 4px rgba(20, 40, 80, 0.25);
    border-radius: 10px;
    color: #fff;
    font-weight: 600;
    font-size: 24px;
    padding: 0px 12px;
}
.ExchangeButton:hover {
    background-color: #27496d;
    cursor: pointer;
}
.ExchangeButton:active {
    box-shadow: none;
}

.CryptoChart {
    width: 100%;
    height: 660px;
    background-color: darkslateblue;
    border-radius: 10px;
}
.CryptoChart__container {
    width: 80%;
    margin: auto;
    padding: 24px 0px;
}
.CryptoChart__header {
    display: flex;
    justify-content: space-between;
}
.CryptoChart__result {
    font-weight: 600;
    font-size: 24px;
    color: #00a8cc;
    padding-bottom: 36px;
}
.CryptoChart__period {
    display: flex;
}

.CryptoChartPeriodValue {
    color: #fff;
    font-size: 18px;
    font-weight: 400;
    padding-left: 20px;
    position: relative;
}
.CryptoChartPeriodValue:hover {
    cursor: pointer;
}
.CryptoChartPeriodValue:before {
    position: absolute;
    content: '';
    background-color: #27496d;
    top: 30px;
    left: 17px;
    width: 70%;
    height: 1px;
    display: none;
}
.CryptoChartPeriodValue:hover::before {
    display: block;
}
.CryptoChartPeriodValue-active::after {
    content: '';
    position: absolute;
    top: 30px;
    left: 17px;
    width: 70%;
    height: 1px;
    background-color: #00a8cc;
}


.HeaderLogo {
    color: #fff;
    font-family: 'Oswald', sans-serif;
    display: flex;
    flex-direction: column;
    text-align: center;
    width: 250px;
}
.HeaderLogo__title {
    font-weight: 400;
    font-size: 36px;
    width: 250px;
}
.HeaderLogo__subtitle {
    font-weight: 300;
    font-size: 24px;
    width: 250px;
}

.Header {
    background-color: darkslateblue;
    width: 100%;
    height: 120px;
    display: flex;
    align-items: center;
}
.Header__container {
    display: flex;
    justify-content: space-between;
}
.Header__navbar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    text-align: center;
    color: #fff;
    font-size: 24px;
    font-weight: 600;
    width: 500px;
}

